function getNationality() {
  const nationalitySelect = document.getElementById("nationality");
  const selectedNationality =
    nationalitySelect.options[nationalitySelect.selectedIndex].value;
  return selectedNationality;
}

function nationalityList() {
  const nationalitySelect = document.getElementById("nationality");
  nationalities.forEach(function (nationality) {
    let option = document.createElement("option");
    option.value = nationality;
    option.text = nationality;
    nationalitySelect.appendChild(option);
  });
}

nationalityList();

// Submit function
function submitForm() {
  const nationality = getNationality();
  const living = document.getElementById("livingGermany").value;
  const anmeldung = document.getElementById("anmeldung").value;
  const message = document.getElementById("message");
  if (living === "Yes") {
    if (anmeldung === "Yes") {
      message.textContent = "The sky is the limit";
    } else {
      message.textContent = "Almost there";
    }
  } else {
    message.textContent =
      "Your only option is WISE or wait until you arrive to Germany";
  }

  console.log(`Nationality: ${nationality}`);
  console.log(`Living in Germany: ${living}`);
}
